code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
    box-sizing: border-box;
}
main {
    flex: 1;
}
.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

p {
    line-height: 1.5;
    color: #0b202a;
}

.container {
    width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.flex-container {
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    padding-right: 15px;
    padding-left: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.row {
    display: flex;
    flex-wrap: wrap;
    flex: 1 1 auto;
    /* margin-left: -15px;
    margin-right: -15px; */
    width: 100%;
    justify-content: space-between;
}

header {
    background-color: #56a8c8;
    width: 100%;
}

footer {
    width: 100%;
    height: 100px;
    background-color: #56a8c8;
}
/* .row > [class^='col-'] {
    padding-left: 15px;
    padding-right: 15px;
} */

h1 {
    margin-top: 56px;
    color: #56a8c8;
}

h2 {
    margin-top: 56px;
    font-size: 2em;
    font-weight: bold;
    color: #56a8c8;
}

h3 {
    text-align: center;
    color: #0b202a;
}

.btn-default {
    padding: 14px 16px;
    background-color: #56a8c8;
    color: #f0f0f0;
    border-radius: 8px;
    border: none;
}

.mg-center {
    margin: 0 auto;
}
.btn-default:hover {
    background-color: #ffdbc9;
    color: #0b202a;
}

